/* TEXT MIXINS
---------------- */
/* TEXT STYLES
--------------- */
html, code {
  font-family: "Open Sans", Helvetica, Arial, sans-serif;
  font-size: 8px;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-kerning: normal;
  font-synthesis: none;
}

body {
  color: #37363b;
  margin: 0;
  font-size: 2.25rem;
}

p {
  margin-top: 0;
}

p:not(:last-child) {
  margin-bottom: 4rem;
}

a {
  color: #bf0e0e;
  text-decoration: none;
}

a:hover, a:focus {
  color: #615f6b;
  text-decoration: underline;
}

.text-with-summary a:hover, .text-long a:hover, .text-with-summary a:focus, .text-long a:focus {
  transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -webkit-transform: translateZ(0);
}

em {
  font-style: italic;
}

strong {
  font-weight: 700;
}

.field-caption {
  text-align: center;
  font-size: 2rem;
  font-style: italic;
}

small {
  font-size: 80%;
}

.warning {
  background: none;
  padding: 1rem 2rem;
  margin-bottom: 4rem;
  color: #dd241b;
  border: 1px solid #dd241b;
  text-align: center;
}

.hr, hr {
  border-style: solid;
  border-width: 1px 0 0;
  color: currentColor;
}

.hr + *, hr + * {
  margin-top: 4rem;
}

blockquote {
  margin: 0 0 2rem;
  max-width: 736px;
  position: relative;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-style: italic;
  font-size: 3rem;
  padding-bottom: 2rem;
}

@media (min-width: 800px) {
  blockquote {
    font-size: 4.5rem;
    padding-bottom: 0;
  }
}

blockquote p {
  display: inline;
}

blockquote .quote-pre {
  color: rgba(35, 35, 35, 0.7);
  font-size: 14rem;
  line-height: .1rem;
  margin-right: 2rem;
  vertical-align: -6.4rem;
}

blockquote .quote-post {
  color: rgba(35, 35, 35, 0.7);
  content: "”";
  font-size: 8rem;
  line-height: .1rem;
  vertical-align: -3.2rem;
  display: inline-block;
}

.text-with-summary::after, .text-long::after {
  clear: both;
  content: "";
  display: table;
}

.text-with-summary > *:last-child, .text-long > *:last-child {
  margin-bottom: 0;
}

.text-with-summary + *, .text-long + * {
  clear: both;
}

.text-with-summary article:not(:last-child), .text-long article:not(:last-child) {
  margin-bottom: 4rem;
}

